
import {set_Account,set_Location,Checkout_Payment_Verified} from '../actions/types'

const initialState={
    accountState:false,
    locationState:{state:false,index:100,create:false},
    payment:false,

}
function CheckoutReducer(state=initialState,action) {
    switch(action.type){
        case set_Account:
            return {...state,
                accountState:action.payload
            }
        
            
        case set_Location:
            return {...state,
                locationState:{state:action.payload.state,
                                index:action.payload.index,
                                create:action.payload.create}
            }

        case Checkout_Payment_Verified:
            return {...state,payment:action.payload}

       
        
        default:
            return state

    }
}

export default CheckoutReducer
