import React,{useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import Loading from '../components/Loading'


import ProductDetailCard from '../components/ProductDetailCard'
import {fetchProductDetail} from '../Redux/actions/ProductActions'


function ProductDetail({pk}) {
    const dispatch = useDispatch()
    const productData=useSelector(state=>state.product)
    useEffect(()=>{
        dispatch(fetchProductDetail(pk))
    },[])
        return productData.DetailLoading?(
            <Loading/>
        ):productData.DetailError?(
             <h2>{productData.DetailError}</h2>
        ):productData.ProductDetail!=null&&
        <div>
           <br/><br/><br/>
            <ProductDetailCard product={productData.ProductDetail}/>
        </div>
}



export default ProductDetail