import React,{useState,useEffect} from 'react'
import {connect,useDispatch,useSelector} from 'react-redux'
import {LoginOTPVerify,signUpOTPVerify} from '../Redux/actions/UserActions'
import {LoginOTP,signUpOTP} from '../Redux/actions/UserActions'
import {useFormik} from 'formik'








export function Otp({LoginVerify,SignUpVerify,LoginError,LoginLoading,location,username,name}) {

    const [timer,setTimer]=useState({seconds:30,intervalID:0})
    const dispatch = useDispatch()
    const whatsappLoading=useSelector(state=>state.user.whatsappLoading)

    const reset=()=>{
        setTimer(state=>{return{...state,seconds:30}})
        var intervalId=setInterval(()=>{
            setTimer(state=>{return{...state,seconds:state.seconds-1}})
            
        },1000)
        setTimer(state=>{return{...state,intervalID:intervalId}})
    }
    useEffect(()=>{
        const intervalId=setInterval(()=>{
            setTimer(state=>{return{...state,seconds:state.seconds-1}})
        },1000)
        setTimer(state=>{return{...state,intervalID:intervalId}})
        return ()=>{
            clearInterval(intervalId)
        }
    },[])

    if(timer.seconds<=0){
        clearInterval(timer.intervalID)
    }

    

    const initialValues={
       otp1:'',
       otp2:'',
       otp3:'',
       otp4:''
    }

    
    const moveOnMax=(field,nextFieldId)=>{
        var e=document.getElementById(field)
        if(e.value.length>=e.maxLength){
            document.getElementById(nextFieldId).focus();
        }
        
    }

    const backOnNull=(event,currentFieldId,prevFieldId)=>{
        var e=document.getElementById(currentFieldId)
        var key=event.keyCode || event.charCode
        if((key==8 || key== 46)&&e.value.length==0){
            document.getElementById(prevFieldId).focus();
        }
        
    }
    
    const onSubmit=values=>{
        if(location=='SignUP'){
            SignUpVerify(username,values.otp1+values.otp2+values.otp3+values.otp4)
        }
        else if(location=='Login'){
            LoginVerify(username,values.otp1+values.otp2+values.otp3+values.otp4)
        }
    }
    const resend=(username,name)=>{
        if(location=='SignUP'){
            dispatch(signUpOTP(username,name))
        }
        else if(location=='Login'){
            dispatch(LoginOTP(username,name))
        }
        reset()
    }

    const validate= values=>{
      
        let errors={}
    
        if(!values.otp1){
            errors.otp1= 'Required'
        }
        else if(!/^[0-9]*$/.test(values.otp1)){
            errors.otp1='Only Digits Allowed'
        }
        else if(values.otp1.length>1){
            errors.otp1='Enter only one digit'
        }

        if(!values.otp2){
            errors.otp2= 'Required'
        }
        else if(!/^[0-9]*$/.test(values.otp2)){
            errors.otp2='Only Digits Allowed'
        }
        else if(values.otp2.length>1){
            errors.otp2='Enter only one digit'
        }

        if(!values.otp3){
            errors.otp3= 'Required'
        }
        else if(!/^[0-9]*$/.test(values.otp3)){
            errors.otp3='Only Digits Allowed'
        }
        else if(values.otp3.length>1){
            errors.otp3='Enter only one digit'
        }

        if(!values.otp4){
            errors.otp4= 'Required'
        }
        else if(!/^[0-9]*$/.test(values.otp4)){
            errors.otp4='Only Digits Allowed'
        }
        else if(values.otp4.length>1){
            errors.otp4='Enter only one digit'
        }
    
        return errors
    }

  const formik=useFormik({
    initialValues,
    onSubmit,
    validate
})



    return (
        <div>
        <form onSubmit={formik.handleSubmit}>
           
                <h3 className="text-center">Enter the OTP</h3>
          
            <div className="row">
                
                <div className="col ">
                    <input className="bg-primary2 border border-primary1 rounded text-center" placeholder="-" style={{width:"30px"}} type='text' id='otp1' name='otp1' {...formik.getFieldProps('otp1')} maxLength="1" onKeyUp={()=>moveOnMax('otp1','otp2')} onKeyDown={(event)=>backOnNull(event,'otp1','otp1')}/>
                    {formik.errors.otp1 && formik.touched.otp1 && <div style={{color:"red",fontSize:"10px"}}>{formik.errors.otp1}</div>}
                </div>
                <div className="col ">
                    <input className="bg-primary2 border border-primary1 rounded text-center" placeholder="-" style={{width:"30px"}} type='text' id='otp2' name='otp2' {...formik.getFieldProps('otp2')} maxLength="1" onKeyUp={()=>moveOnMax('otp2','otp3') } onKeyDown={(event)=>backOnNull(event,'otp2','otp1')}/>
                    {formik.errors.otp2 && formik.touched.otp2 && <div style={{color:"red",fontSize:"10px"}}>{formik.errors.otp2}</div>}
                </div>
                <div className="col ">
                    <input className="bg-primary2 border border-primary1 rounded text-center" placeholder="-" style={{width:"30px"}} type='text' id='otp3' name='otp3' {...formik.getFieldProps('otp3')} maxLength="1" onKeyUp={()=>moveOnMax('otp3','otp4')} onKeyDown={(event)=>backOnNull(event,'otp3','otp2')}/>
                    {formik.errors.otp3 && formik.touched.otp3 && <div style={{color:"red",fontSize:"10px"}}>{formik.errors.otp3}</div>}
                </div>
                <div className="col ">
                    <input className="bg-primary2 border border-primary1 rounded text-center" placeholder="-" style={{width:"30px"}} type='text' id='otp4' name='otp4' {...formik.getFieldProps('otp4')} maxLength="1" onKeyDown={(event)=>backOnNull(event,'otp4','otp3')} />
                    {formik.errors.otp4 && formik.touched.otp4 && <div style={{color:"red",fontSize:"10px"}}>{formik.errors.otp4}</div>}
                </div>
            </div>
            <br/>
            

            {LoginLoading || whatsappLoading?
                 <button className="btn btn-primary1" type="button" disabled>
                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Loading...
                </button>
               :
               <button className="btn btn-primary1" type="submit">Continue</button>
               }
                   
             {LoginError&&<p className="text-danger">{LoginError}</p>}
            
        </form>
        <div >
                {!whatsappLoading &&
                 <div> (Did Not Get OTP?  {timer.seconds>0?<p style={{display:"inline"}}>Resend in {timer.seconds} )</p>:<a href='#' onClick={()=>resend(username,name)}><b>Resend Now</b>)</a>} </div>}
            </div>
    </div>
    )
}

const mapStateToProps = state=>{
    return{LoginLoading:state.user.loading,
           LoginError:state.user.errorMessage,
           LoginErrorInfo:state.user.errorInfo,

          }
  }
  const mapDispatchToProps=dispatch => {
      return{
          SignUpVerify:(username,otp)=>dispatch(signUpOTPVerify(username,otp)),
          LoginVerify:(username,otp)=>dispatch(LoginOTPVerify(username,otp))
      }
  }
  
  export default connect(mapStateToProps,mapDispatchToProps)(Otp)
  