import React from 'react'
import {OTP_START,OTP_FAIL,OTP_SUCCESS,AUTH_LOGOUT,START_ADDRESS,
    REMOVE_ADDRESS,GET_ADDRESS,CREATE_ADDRESS_SUCCESS,
    CREATE_ADDRESS_FAIL,GET_ADDRESS_SUCCESS,GET_ADDRESS_FAIL
,Whatsapp_start,Whatsapp_Success,Whatsapp_Fail} from '../actions/types'



const initialState={
    name:'',
    username:'',
    token:null,
    errorMessage:'',
    loading:false,

    addressLoading:false,
    addressInfo:'',
    addressList:[],
    addressExists:false,
    addressError:'',

    whatsappLoading:false,
    whatsappError:'',
    whatsappRes:'',
    whatsappOTP:false
}

export default function UsersReducer(state=initialState,action) {
    switch(action.type){
        case OTP_START:
            
            return {...state,
                errorMessage:null,
                errorInfo:null,
                loading:true,
            }
        case OTP_SUCCESS:
           
            return {
                loading: false,
                errorMessage:null,
                errorInfo:null,
                token:action.payload.token,
                name:action.payload.name,
                username:action.payload.username
            }
        case OTP_FAIL:
           
            return {...state,
                errorMessage:action.payload,
                loading:false,
                
            }
        case AUTH_LOGOUT:
           
            
            return{
                ...state,
                token:null,
                name:''
            }
        case START_ADDRESS://Start Address Create
            return{
                ...state,
                addressLoading:true
            }
        case CREATE_ADDRESS_SUCCESS:
            return{
                ...state,
                loading:false,
                errorMessage:null,
                errorInfo:null,
                addressInfo:action.payload
            }
        case CREATE_ADDRESS_FAIL:
            return{
                ...state,
                loading:false,
                errorMessage:action.payload
            }
        
        case GET_ADDRESS_SUCCESS:
            return{
                ...state,
                addressError:null,
                addressList:action.payload,
                addressExists:true,
                addressLoading:false
            }
            
        case GET_ADDRESS_FAIL:
            return{
                ...state,
                addressLoading:false,
                addressError:action.payload
            }

        case Whatsapp_start:
            return{
                ...state,
                whatsappLoading:true
            }

        case Whatsapp_Success:
            return{
                ...state,
                whatsappLoading:false,
                whatsappRes:action.payload,
                whatsappError:null
            }

            case Whatsapp_Fail:
                return{
                    ...state,
                    whatsappLoading:false,
                    whatsappError:action.payload,
                    whatsappRes:null
                }
        
        default:
            return state;
    }
}
