
import store from '../store'
import axios from 'axios'
import {set_Account,set_Location,Checkout_Payment_Verified} from './types'
import {BASE_URL} from './types'
export const setCheckoutAccount=(accState)=>{
    return({
        type:set_Account,
        payload:accState
    })
}

export const setLocation=(state,create,index=100)=>{
    return({
        type:set_Location,
        payload:{
            state:state,
            index:index,
            create:create
        }
    })

}

export const setPayVerified=()=>{
    return({
        type:Checkout_Payment_Verified,
        payload:true
    })

}

export const addressFail=()=>{
    return({
        type:Checkout_Payment_Verified,
        payload:true
    })

}




  //select the delivery address in the backend

  export const setDeliveryAddress=(index)=>{
    const token=store.getState().user.token
    return dispatch=>{
        axios.post(BASE_URL+`/api/selectaddress/${index}`,{},{
            headers: {'Authorization': `Token ${token}`,
            }
        }
        )
        .then(function (response) {
       
        dispatch(setLocation(true,false,index));
        })
        .catch(function (error) {
      
        });

    }
    
  }



