import React,{useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {delCartProducts,UpdateItem} from '../Redux/actions/CartActions'


export default function TheCart() {
    var total=0
    const products=useSelector((state=>state.cart.CartProducts))
    const cartLoading=useSelector(state=>state.cart.CartLoading)
    const dispatch=useDispatch()
    
    useEffect(() => {
         total=0
    },[])
  
    return (
        <React.Fragment>
        
           <div id="theCart"  className={` hiddenCart bg-primary2 container overflow-auto`} >
            <div id="theCartContent" className="hiddenCartContent">
               <div className="row ">
               
                   <div className=" text-center col-3-lg ml-3">
                        <h3  className="mt-4">Order Summary</h3>
                   </div>
                   <div className="col-3-lg ml-3">
                       {cartLoading &&
                        <div className="spinner-border mt-4" role="status">
                            <span className="sr-only">Loading...</span>
                        </div>
                       }
                     </div>
                   
                   
                   <div style={{width:"100px"}} className="col-7-lg ">
                       
                   </div>
                   <div className="col">
                  
                   <button onClick={()=>{
                    const cart=document.getElementById("theCart")
                    const cartContent=document.getElementById("theCartContent")
                    cart.classList.toggle('showCart')
                    cartContent.classList.toggle('showCartContent')
                   }} 
                     type="button" className=" my-3 close" aria-label="Close">
                  
                        <span aria-hidden="true">&times;</span>
                      </button>
                   </div>
                   
               </div>
                <hr/>
             
            {
               products.map((item,index)=>{
                   total+=((item.price*item.quantity*item.weight)/1000)
                    return(
                        <React.Fragment key={index}>
                        
                        <div  className="card mb-3 border-primary1 mt-2 cart-card" >
                            <div  className="row no-gutters">
                                <div className="col-6 border-primary1 cart-card">
                                <a style={{textDecoration: "none",color:"black"}} href={`/detail/${item.id}`}>
                                <img src={item.img} className="card-img cart-img" alt="..."/>
                                </a>
                                </div>
                                <div className="col-5 bg-primary1">
                                <div className="card-body border-primary1">
                                    <h6 className="card-title font-weight-bold">{item.title}</h6>
                                    <p className="card-text"><b>{item.weight}</b> grams</p>
                                    <p className="card-text"><b>{(item.price*item.quantity*item.weight)/1000}</b></p>

                                    <div className="btn-group  bg-primary2 rounded-top rounded-bottom" role="group" aria-label="Basic example">
                                        <button style={{width:"25px",height:"40px"}}  onClick={()=>dispatch(UpdateItem(index,item.quantity-1))}  type="button" className="btn btn-primary2  border-primary1"><p className="">-</p></button>
                                            <div style={{width:"25px",height:"40px"}} className=" pt-1 text-center">{item.quantity}</div>
                                        <button style={{width:"25px",height:"40px"}} onClick={()=>dispatch(UpdateItem(index,item.quantity+1))} type="button" className="btn border-primary1">+</button>
                                    </div>

                                </div>
                                </div>
                            <div  className="col-1 bg-primary1">
                                    <button onClick={()=>dispatch(delCartProducts(index))} type="button" className="close" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                             </button>
                                </div>
                            </div>
                        </div>
                      
                        
                        </React.Fragment>

                        )

               })}
               {!cartLoading && (total==0? <h5>No Items In Cart</h5>:<div> <h5 className="font-weight-bold">Total {  total  } </h5><a style={{color:"black"}} href="/checkout/"> <b>Proceed to Checkout </b> </a> </div>)}
               </div>
                </div>
                

                </React.Fragment>

          
    )
}
