import {Item_Success,Add_item,Del_item,Set_Cart_Products,Update_Item,Start_Item,Item_Fail,change_quantity} from './types'
import {getCookie} from '../../components/Cookies'
import store from '../store'
import axios from 'axios'
import {BASE_URL} from './types'

export const Additem=productInfo=>{ //Get the title and price
   
    return({
        type:Add_item,
        payload:productInfo
    }
    )
}
export const changeQuantity=(index,quantity)=>{
    return({
        type:change_quantity,
        payload:{index:index,quantity: quantity}
    })
}
export const Updateitem=index=>{
    return({
        type:Update_Item,
        payload:index
    })
}

export const Delitem=productIndex=>{
    return({
        type:Del_item,
        payload:productIndex
    }
    )
}

export const StartItem=()=>{
    return({
        type:Start_Item
    })
}
export const ItemSuccess=()=>{
    return({
        type:Item_Success
    })
}


export const ItemFail=(error)=>{//some API call was Successful
    return({
        type:Item_Fail,
        payload:error
    })
}


export const setCartProducts=CartProducts=>{
    return({
        type:Set_Cart_Products,
        payload:CartProducts
    })
}

export const getCartProducts=()=>{ //fetch from coookies
    var products
        try{
             products = JSON.parse(getCookie("CartProducts"))  
            
             if( products.length>0){return products}
             else{return false}
        }
        catch(err){
            return false 
        }
}

export const loginCopyCart=()=>{ // upon login if items are there in the cart then copy them to the backend
    const products=getCartProducts()
    return dispatch=>{
        if(products){ //copy the items to the login cart backend
          
            dispatch(delCartProducts(999,products))
        }
       else{
      
        dispatch(getOrderItems())
       }
        }
    }

export const UpdateItem=(index,quantity)=>{
    const token=store.getState().user.token
    return dispatch=>{
        dispatch(StartItem())
      
        //Update the backend product with the given index
                if(token && quantity>0){ //if Signed in update the backend
                axios.put(BASE_URL+`/api/orderitem/${index}`, {
                    quantity:quantity
                    },{
                        headers: {'Authorization': `Token ${token}`,
                        }
                    }
                    )
                    .then(function (response) {
                        dispatch(changeQuantity(index,quantity))
                    })
                    .catch(function (error) {
                    dispatch(ItemFail(error.response.data))
                    });
                }
                else{ //only locally change the state
                    dispatch(changeQuantity(index,quantity))
                }

    }

}
export const addCartProduct=(productInfo)=>{ //add to backend
    const CartProducts=store.getState().cart.CartProducts
    const token=store.getState().user.token
    return dispatch=>{
            const addedItem={...productInfo} // new item to be added 
            
                //Add new backend product
                dispatch(StartItem())

               if(token){
                axios.post(BASE_URL+'/api/orderitem/0', [{
                    title: productInfo.title,
                    quantity: productInfo.quantity,
                    weight: productInfo.weight,
                    }],{
                        headers: {'Authorization': `Token ${token}`,
                        }
                    }
                    )
                    .then(function (response) {
                        dispatch(Additem(productInfo))
                    })
                    .catch(function (error) {
                        dispatch(ItemFail(error.response.data))
                   
                    });
               }
                else{
                    dispatch(Additem(productInfo))
                }
           // }
     }
}

export const delCartProducts=(index,products)=>{
    const token=store.getState().user.token
    return dispatch=>{
       
        dispatch(StartItem())
        if(token){
            axios.delete(BASE_URL+`/api/orderitem/${index}`,
                        {
                        headers: {'Authorization': `Token ${token}`,
                        }
                    }
                    )
                    .then(function (response) {
                        if(index==999){
                            //upadte the cart after deleting the backend products
                            
                            const newProducts=products.map(items=>{
                                return(
                                        {
                                        title: items.title,
                                        quantity: items.quantity,
                                        weight: items.weight,
                                        }           
                                )
                                })
                            dispatch(copyCookieProducts(newProducts))//copy products to the backend
                        }
                        dispatch(Delitem(index))
                 
                    })
                    .catch(function (error) {
                        dispatch(ItemFail(error.response.data))
                   
                    });
        }
        else{
            dispatch(Delitem(index))
        }
    }
}

export const getOrderItems=()=>{ //fetch from backend
    const token=store.getState().user.token
    return dispatch=>{
        dispatch(StartItem())
       
        if(token){
            axios.get(BASE_URL+`/api/orderitem/0`,
                        {
                        headers: {'Authorization': `Token ${token}`,
                        }
                    }
                    )
                    .then(function (response) {
                       
                        if(response.data.length>0){

                            response.data.map(items=>{
                                const CartInfo={"id":items.product.id,
                                                "title":items.product.title,
                                                "price":items.product.price,
                                                "img":BASE_URL+`${items.product.img}`,
                                                "category":items.product.category,
                                                "weight": items.weight,
                                                "quantity":items.quantity}
                                              
                                dispatch(Additem(CartInfo))

                            })}
                            else{
                                dispatch(ItemSuccess())
                            }
                        
                    })
                    .catch(function (error) {
                       
                        dispatch(ItemFail(error.response.data))
                    });
        }
    }
}

export const copyCookieProducts=(productList)=>{
    const token=store.getState().user.token
   
   //copy the entire list to my backend
    return dispatch=>{
                //Add new backend product
                axios.post(BASE_URL+'/api/orderitem/0', productList
                ,{
                        headers: {'Authorization': `Token ${token}`,
                        }
                    }
                    )
                    .then(function (response) {
                       
                        dispatch(ItemSuccess())
                    })
                    .catch(function (error) {
                    
                    });
            }
     }