import React,{useState,useEffect} from 'react'
//compoenents for the login modal
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
//
import {logout} from '../Redux/actions/UserActions'
import {useDispatch,useSelector} from 'react-redux'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'

import SignUpFormWhatsapp from '../components/SignUpFormWhatsapp'
import LoginFormWhatsapp from '../components/LoginFormWhatsapp'
import store from '../Redux/store'
//Redux Actions
import {autoCheckState} from '../Redux/actions/UserActions' //Check if user is logged in on page load.

//Cookie functions
import {setCookie} from '../components/Cookies'
//Main Css
import '../components/Component.css'
import '../components/Tab1.css'
import '../media/style/main.css'


export default function Login({csrf}) {
  const dispatch=useDispatch()
  const authenticated=useSelector(state=>state.user.token!=null)//Check if user is logged in
  useEffect(()=>{
    dispatch(autoCheckState())
  },[])


  //Whenever the store is changed. Set the products to the cookies
  store.subscribe(()=>{
    const current_state=JSON.stringify(store.getState().cart.CartProducts)
    setCookie("CartProducts",current_state,1)
})

    const [key, setKey] = useState('Sign In'); // For tab Buttons
    //To trugger the modal
    const [show,setShow]=useState(false)
    const name=useSelector(state=>state.user.name)
    const handleClose=()=>setShow(false)
    const handleShow=()=>{
      if(authenticated){
        dispatch(logout())
      }
      else{
        setShow(true)
      }
      
    }
    

    return (
       <React.Fragment>
         {authenticated? <Button className="btn btn-primary1" onClick={handleShow}>LOGOUT</Button>  : <Button className="btn btn-primary1" onClick={handleShow}>LOGIN</Button> }
           

            <Modal style={{zIndex:"2004"}} show={show} onHide={handleClose}>
                <Modal.Header style={{border:"none"}} closeButton className="bg-primary2">
                <Modal.Title>
                  <Row style={{width:"450px"}}>
                    <Col >
                      {name?<h4 className="font-weight-bold">Welcome <p style={{textShadow : "1px 1px gray"}} className="text-primary1">{name.toUpperCase()}</p></h4>:
                      <h4>WELCOME</h4>}
                    </Col>
                  </Row>
                </Modal.Title>
                </Modal.Header>

                <Modal.Body className="bg-primary2">
                  

                <Tabs
                  id="controlled-tab-example"
                  activeKey={key}
                  onSelect={(k) => setKey(k)}
                >
                  <Tab eventKey="Sign In" title="Sign In">

                    <LoginFormWhatsapp authenticated={authenticated}/>
                   
                  </Tab>
                  <Tab  eventKey="Sign Up" title="Sign Up">
                    <SignUpFormWhatsapp authenticated={authenticated}/>
                    
                  </Tab>
                </Tabs>

                </Modal.Body>
            </Modal>
        </React.Fragment>
    )
}

