import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import store from './Redux/store'

import ProductDetail from './containers/ProductDetail'
import Checkout from './containers/Checkout'
// ALWAYS RENDERED
import Login from './containers/Login'
import TheCart from './components/TheCart'
// ALWAYS RENDERED

const productDetail=document.getElementById('productDetail')
const checkout=document.getElementById('checkout')
//Always being rendered
const login=document.getElementById('login')
const cart=document.getElementById('thecartelement')
//Always being rendered


const createElement=React.createElement

// ReactDOM.render(
//   <Provider store={store}>
//      <App />
//   </Provider>,
//   document.getElementById('root')
// );


//Always being rendered

  ReactDOM.render(
    <Provider store={store}>
      {createElement(TheCart,cart.dataset)}
    </Provider>,
    cart
    );



  ReactDOM.render(
    <Provider store={store}>
      {createElement(Login,login.dataset)}
    </Provider>,
    login
    );

//Always being rendered


if(productDetail){
  ReactDOM.render(
    <Provider store={store}>
      {createElement(ProductDetail,productDetail.dataset)}
    </Provider>,
    productDetail
    );
}
if(checkout){
  ReactDOM.render(
    <Provider store={store}>
      {createElement(Checkout,checkout.dataset)}
    </Provider>,
    checkout
    );
}


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
