import {Item_Success,Add_item,Del_item,Start_Item,Item_Fail,change_quantity} from '../actions/types'
import {getCookie} from '../../components/Cookies'



const products =()=> {
        const CurrentCart=getCookie("CartProducts")
        if(CurrentCart==""){
            return([])
        }
        else{
            return(JSON.parse(getCookie("CartProducts")))
        }
    
    // return(JSON.parse(getCookie("CartProducts")))
  }
const initialState={
    CartError:'',
    CartLoading:false,
    CartProducts:products()
}

 const CartReducer=(state=initialState,action)=>{
    switch(action.type){
        case Add_item:
                return({CartLoading:false,
                    CartProducts:[...state.CartProducts,action.payload]})

        case Del_item:
            //Delete backend product
            // return state;
            return{
                CartLoading:false,
                CartProducts:state.CartProducts.filter((item,index)=>index!=action.payload)
            }

        case Start_Item:
           //some api call to backend has started
            return{
                ...state,
                CartLoading:true
            }
        
      
        case Item_Fail:
            //some api call to backend has started
            return{
                ...state,
                CartLoading:false,
                CartError:action.payload

            }

        
        case change_quantity:
            //some api call to backend has started
            if (action.payload.quantity>0){
                return{
                    ...state,
                    CartLoading:false   ,
                        CartProducts:state.CartProducts.map((item,index)=>{
                            if(index==action.payload.index){
                                return({...item,quantity:action.payload.quantity})
                            }
                            else{
                                return item;
                            }
                            
                        })
                    }
            }
            else{
                return {...state,CartLoading:false}
            }
            
            
        case Item_Success:
            return {...state,CartLoading:false}



        default: return state

    }
}

export default CartReducer