import {OTP_START,OTP_FAIL,OTP_SUCCESS,AUTH_LOGOUT,
    START_ADDRESS,CREATE_ADDRESS_SUCCESS,
    CREATE_ADDRESS_FAIL,GET_ADDRESS_SUCCESS,GET_ADDRESS_FAIL,Whatsapp_start,
    Whatsapp_Success,Whatsapp_Fail} from './types'

import axios from 'axios'
import {delCookie} from '../../components/Cookies'
import {loginCopyCart} from './CartActions'
import store from '../store'
import {BASE_URL} from './types'



export const otpStart=()=>{
    return{
        type:OTP_START,
        payload:"START AUTHORIZATION"
    }
}
export const otpSuccess=(info)=>{
    return{
        type:OTP_SUCCESS,
        payload:info
    }
}
export const otpFail=(error)=>{
    return{
        type:OTP_FAIL,
        payload:error
    }
}

export const WhatsappStart=()=>{
    return{
        type:Whatsapp_start
    }
}
export const WhatsappSuccess=(res)=>{
    return{
        type:Whatsapp_Success,
        payload:res
    }
}
export const WhatsappFail=(error)=>{
    return{
        type:Whatsapp_Fail,
        payload:error
    }
}

export const logout=()=>{
    localStorage.removeItem('token');
    localStorage.removeItem(('expirationDate'))
    localStorage.removeItem('addressList')
    localStorage.removeItem('username')
    localStorage.removeItem('name')
    delCookie('CartProducts')
    return{
        type:AUTH_LOGOUT,
        payload:'Logout'
    }
}
const startAddress=()=>{
    return({
        type:START_ADDRESS
    })
}
const createAddressSuccess=(details)=>{
    return({
        type:CREATE_ADDRESS_SUCCESS,
        payload:details //Tag,Address,landmark and houseNumber
    })
}

const createAddressFail=(error)=>{ //error message
    return({
        type:CREATE_ADDRESS_FAIL,
        payload:error 
    })
}

const getAddressSuccess=(AddressList)=>{
    return{
        type:GET_ADDRESS_SUCCESS,
        payload:AddressList
    }
}

const getAddressFail=(error)=>{
    return{
        type:GET_ADDRESS_FAIL,
        payload:error
    }
}


const checkAuthTimeout= expirationTime=>{
    return dispatch=>{
        setTimeout(()=>{
            dispatch(logout());
        },expirationTime*1000)
    }
}

export const signUpOTP=(username,name)=>{
    return dispatch=>{
        dispatch(WhatsappStart())
        axios.post(BASE_URL+'/api/signupOTP/',{
            username: username,
            name:name
        })
            .then(response=>{
                dispatch(WhatsappSuccess(response.data))
               
            })
            .catch(error=>{
               
                dispatch(WhatsappFail(error.response.data))
            })   
    }
}


export const signUpOTPVerify=(username,otp)=>{
    return dispatch=>{
      
        dispatch(otpStart())
        axios.post(BASE_URL+'/api/signup_OTP_verify/',{
            username: username,
            OTP:otp
        })
            .then(response=>{
                const token=response.data.key;
                const name=response.data.name;
                const expirationDate=new Date(new Date().getTime()+3600*1000); //1hour
                localStorage.setItem('token',token);
                localStorage.setItem('name',name);
                localStorage.setItem('username',username);
                localStorage.setItem('expirationDate',expirationDate);
                dispatch(otpSuccess({token:token,name:name,username:username})); //send token to the otpSuccess
                dispatch(loginCopyCart())
                dispatch(checkAuthTimeout(3600));
                dispatch(getDeliveryAddress(token));
                
            })
            .catch(error=>{
                dispatch(otpFail(error.response.data))
            })   
    }
}

