//Check if Token exists and if it does then its a login or else logout
export const autoCheckState=()=>{
    return dispatch=>{
        const token = localStorage.getItem('token')
        const username=localStorage.getItem('username')
        const name=localStorage.getItem('name')
        const addressList=JSON.parse(localStorage.getItem('addressList'))
        if(token==undefined){ //if no tooken then logout
            dispatch(logout())
        }else{// if token exists then use this to check expiration date
            const expirationDate = new Date(localStorage.getItem('expirationDate'))//current expiration date in localstorage
            if(expirationDate<=new Date()){ //if expiration date is over 
                dispatch(logout())
            }
            else{
                dispatch(otpSuccess({token:token,username:username,name:name}))
                dispatch(checkAuthTimeout( (expirationDate.getTime()-new Date().getTime()) /1000 ))
                if( addressList==null){//if addresslist is not there in local storage fetch from backend
                    dispatch(getDeliveryAddress(token));
                }
                else{//if addresslist is present in localStorage then use the current addressList
                    dispatch(getAddressSuccess(addressList))

                }
                
            }
            
        }
    }
}

//To Add a Delivery Address

export const createDeliveryAddress=(tag,address,landmark,houseNumber,addInfo,lat,lng)=>{
    if(tag.length==0){
        tag='My Address'
    } 
    const token=store.getState().user.token
    return dispatch=>{
        dispatch(startAddress())
        axios.post(BASE_URL+'/api/signupaddress/0', {
        tag: tag,
        address: address,
        landmark: landmark,
        houseNumber: houseNumber,
        addInfo:addInfo,
        lat:lat,
        lng:lng
        },{
            headers: {'Authorization': `Token ${token}`,
            }
        }
        )
        .then(function (response) {
      
        dispatch(getDeliveryAddress(token));
        dispatch(createAddressSuccess(response.data))
        })
        .catch(function (error) {
     
        dispatch(createAddressFail(error.message))
        });
  

    }
    
  }
  
  //To get all the delivery addresses from backend

  export const getDeliveryAddress=(token)=>{
    return dispatch=>{
        dispatch(startAddress())
        axios.get(BASE_URL+'/api/signupaddress/0',{
            headers: {'Authorization': `Token ${token}`,
            }
        }
        )
        .then(function (response) {
        const addressList=response.data
        localStorage.setItem('addressList',JSON.stringify(addressList));
        dispatch(getAddressSuccess(addressList))
        
        })
        .catch(function (error) {
       
        dispatch(getAddressFail(error.message))
        });
  

    }
    
  }


  //To delete delivery addresses from backend

  export const delDeliveryAddress=(index)=>{
    const token=store.getState().user.token
    return dispatch=>{
        dispatch(startAddress())
        axios.delete(BASE_URL+`/api/signupaddress/${index}`,{
            headers: {'Authorization': `Token ${token}`,
            }
        }
        )
        .then(function (response) {
      
        dispatch(getDeliveryAddress(token));
        })
        .catch(function (error) {
     
        });
  

    }
    
  }
