import React,{useState,useContext} from 'react'
import {connect} from 'react-redux'
import {LoginOTP} from '../Redux/actions/UserActions'
import {useFormik} from 'formik'
import Otp from './Otp'







export function LoginFormWhatsapp({authenticated,LoginUser,whatsappError,whatsappLoading,whatsappRes}) {

    

    const initialValues={
        Loginusername:'',
    }
    

    const onSubmit=values=>{
        LoginUser(values.Loginusername)
    }

    const validate= values=>{
       
        let errors={}
    
        if(!values.Loginusername){
           
            errors.Loginusername= 'Required'
        }
        else if(!/^[0-9]*$/.test(values.Loginusername)){
            errors.Loginusername='Only Digits Allowed'
        }
        else if(values.Loginusername.length!=10){
            errors.Loginusername='Check PhoneNumber. 10 Digits are required.'
        }
        return errors
    }

  const formik=useFormik({
    initialValues,
    onSubmit,
    validate
})



    return (
        <div>
            {authenticated?<h3 className="mt-4">Enjoy Your 10DerMeatz Experience! </h3>:
            whatsappRes?
            <div>
                <Otp location="Login" name={formik.values.name} username={formik.values.Loginusername}/>
                <b className="text-center text-muted">{whatsappRes}</b>
            </div>
            :
        <form onSubmit={formik.handleSubmit}>

            <label htmlFor='Loginusername'>Phone Number</label>
            <input className="bg-primary2 border border-primary1 rounded w-50"  style={{display:"block"}} type='tel' maxLength='10' id='Loginusername' name='Loginusername' {...formik.getFieldProps('Loginusername')} />
            {formik.errors.Loginusername && formik.touched.Loginusername?<div style={{color:"red",fontSize:"10px"}}>{formik.errors.Loginusername}</div>:<small id="Loginusername" className="form-text text-muted">(Enter Your Registered Phone Number)</small>}
           <br/>
           <p className="text-danger">{whatsappError}</p>
           

            
            {whatsappLoading?
                 <button className="btn btn-primary1" type="button" disabled>
                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Loading...
                </button>
               :
                <button type="submit" className="btn btn-primary1">Sign In</button>
               }
            
        </form>}
    </div>
    )
}

const mapStateToProps = state=>{
    return{
        whatsappLoading:state.user.whatsappLoading,
        whatsappError:state.user.whatsappError,
        whatsappRes:state.user.whatsappRes

          }
  }
  const mapDispatchToProps=dispatch => {
      return{
          LoginUser:(username)=>dispatch(LoginOTP(username))
      }
  }
  
  export default connect(mapStateToProps,mapDispatchToProps)(LoginFormWhatsapp)
  