import axios from 'axios';
import store from '../store'
import {Get_Detail_Product,Get_Detail_Success,Get_Detail_Fail} from './types'
import {BASE_URL} from './types'
//Get Products
const getDetailProduct=()=>{
    return({
        type:Get_Detail_Product,
        payload: "GET CATEGORY PRODUCTS"
    })
}

//Get detail Succeeded
const getDetailSuccess=product=>{
    return({
        type: Get_Detail_Success,
        payload: product
    })
}
//Get detail fail
const getDetailFail=error=>{
    return({
        type: Get_Detail_Fail,
        payload: error
    })
}


export const fetchProductDetail= (pk)=>{
    return (dispatch)=>{
        dispatch(getDetailProduct())
       // console.log(BASE_URL+'/apk/${pk}')
        axios.get(BASE_URL+`/api/${pk}`)
        .then(response=>{
            dispatch(getDetailSuccess(response.data))
        })
        .catch(error=>{
            dispatch((getDetailFail(error.message)))
        })
    }
}



