import {Get_Detail_Product,Get_Detail_Success,Get_Detail_Fail,
    Get_Checkout_Product,Get_Checkout_Success,Get_Checkout_Fail} from '../actions/types'

const initialState={
    ProductDetail:{},
    DetailLoading:false,
    DetailError:'',

}

 const ProductReducer=(state=initialState,action)=>{
    switch(action.type){
        case Get_Detail_Product:
            return {...state,
                DetailLoading:true}

        case Get_Detail_Success:
            return {...state,
                ProductDetail:action.payload,
                DetailError:null,
                DetailLoading:false
            }   

        case Get_Detail_Fail:
            return {...state,
                DetailError:action.payload,
                ProductDetail:{},
                DetailLoading:false
            }
        default: return state
    }

}
export default ProductReducer