const MarkerSVG=()=><svg  height="30pt" viewBox="-96 0 464 464" width="30pt" xmlns="http://www.w3.org/2000/svg"><path d="m272 428c0-19.882812-60.890625-36-136-36s-136 16.117188-136 36 60.890625 36 136 36 136-16.117188 136-36zm0 0" fill="#adabac"/><path d="m120 160h32v256c0 8.835938-7.164062 16-16 16s-16-7.164062-16-16zm0 0" fill="#494342"/><path d="m232 96c0 53.019531-42.980469 96-96 96s-96-42.980469-96-96 42.980469-96 96-96 96 42.980469 96 96zm0 0" fill="#ad2943"/><path d="m200 96c0 35.347656-28.652344 64-64 64s-64-28.652344-64-64 28.652344-64 64-64 64 28.652344 64 64zm0 0" fill="#ee3446"/></svg>
Geocode.setApiKey(API_KEY)
const intialState={
    center: {
        lat:12.972442,lng:77.580643
      },
      zoom: 16,
      loading:false,
}
export default function Map() {
    const token=useSelector(state=>state.user.token)
    const loading=useSelector(state=>state.user.loading)
    const dispatch = useDispatch()
    const [state,setState]=useState(intialState)

    const initialValues={  
                        address:'',
                        landmark:'',
                        housenumber:'',
                        tag:'',
                        DeliveryInfo:''
                    }

   
    const validate= values=>{
        let errors={}
        if(!values.address){
            errors.address= 'Required'
        }
        if(!values.landmark){
            errors.landmark= 'Required'
        }
        if(!values.housenumber){
            errors.housenumber='Required'
        }
       
        return errors
    }

    
    const onSubmit=values=>{
       
       
        dispatch(createDeliveryAddress(
            values.tag,values.address,values.landmark,values.housenumber,values.DeliveryInfo,state.center.lat,state.center.lng
        ))
        dispatch(setLocation(false,false,))
    }

    const formik=useFormik({
        initialValues,
        onSubmit,
        validate
    })
    //console.log("Errors",formik.errors)
    // console.log("values",formik.values)
    // console.log("visited fields",formik.touched)

   
    //get the location rn
    const handleTarget=()=>{

        if (navigator.geolocation) {
            setState(prevState=>{
                return({
                    ...prevState,loading:true
                })
            })
          navigator.geolocation.getCurrentPosition(position=>{
              setState(prevState=>{
                return(
                    {
                        ...prevState,center:{
                            lat: position.coords.latitude,
                            lng: position.coords.longitude
                        },loading:false
                    }
                )
               })
               getGeoCode(state.center.lat, state.center.lng)
          })
          }
          else {
            alert('Geolocation is not supported for this Browser/OS.');
          }

    }


    const handleChange=(event)=>{
       
        setState(prevState=>{
            return(
                {
                    ...prevState,address:event.target.value
                }
            )
        })
    }
    const onPlaceSelected=(place)=>{
       
        try{
            let newLat=place.geometry.location.lat()
            let newLng=place.geometry.location.lng()
            formik.setFieldValue("address",place.formatted_address)
            setState(prevState=>{
                return(
                    {
                        ...prevState,center:{
                            lat: newLat,
                            lng: newLng
                        }
                    }
                )
               })
        }
        catch (error){
            
        }
        
    }

    const getGeoCode=(newLat,newLng)=>{
        Geocode.fromLatLng(newLat,newLng).then(res => {
           
            formik.setFieldValue("address",res.results[0].formatted_address)
        })}

    const handleApiLoaded = (map, maps) => {
        // use map and maps objects
        maps.event.addListener(map, "dragend", function() {
            var newCoordinates=map.getCenter().toUrlValue()
            var [lat,lng]=newCoordinates.split(",")
            setState(prevState=>{
                return(
                    {
                        ...prevState,center:{lat:lat,lng:lng}
                    }
                )
            })
            getGeoCode(lat, lng)
         })
      };

    const Spinner = () =>
    <div className='mapSpinner'>
        <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
        </div>
    </div>
    
      
    


    return (
        <div className="bg-primary2">
        <div className="map ">
            <div className="marker">
                <MarkerSVG/>
            </div>
            <div className="target">
                <button  onClick={()=>handleTarget()} ><TargetSVG /></button> 
            </div>
        {state.loading?<Spinner/>:null}
        
        <GoogleMapReact
          bootstrapURLKeys={{
               key: API_KEY,
               region:'IND'
             }}
          center= {state.center}
          defaultZoom={state.zoom}
          yesIWantToUseGoogleMapApiInternals
          onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
          
        >
            
        </GoogleMapReact>
       </div>
       <div className='container-fluid '>
        <form  onSubmit={formik.handleSubmit}>
             <div className='row my-3'>
                 <div className="col">
                 <label htmlFor='address'>Address</label>
                <AutoComplete
                onChange={handleChange}
                className="d-block border-top-0 border-left-0 border-right-0 border-warning bg-primary2 w-100 "
                componentRestrictions={{country: "ind"}}
                types={['(regions)']}
                onPlaceSelected={onPlaceSelected}
                // value={state.address}
                id='address' 
                name='address' 
                {...formik.getFieldProps('address')}
                
                />
                {formik.errors.address && formik.touched.address?<div style={{color:"red",fontSize:"10px"}}>{formik.errors.address}</div>:null}

                 </div>
                 <div className="col">
                 <label htmlFor='housenumber'>FLAT/HOUSE NO.</label>
                <input className=" bg-primary2 border-top-0 border-left-0 border-right-0 border-warning w-100 " placeholder="Eg: #120 Royal Enclave" style={{display:"block"}} type='tel' id='housenumber' name='housenumber' {...formik.getFieldProps('housenumber')} />
                {formik.errors.housenumber && formik.touched.housenumber?<div style={{color:"red",fontSize:"10px"}}>{formik.errors.housenumber}</div>:null}
                 </div>
             </div>

             
             <div className="row my-3">
                 <div className="col">
                 <label htmlFor='landmark'>Landmark</label>
                <input className="bg-primary2 border-top-0 border-left-0 border-right-0 border-warning w-100" placeholder="Eg: Near cubbbon park" style={{display:"block"}} type='text' id='landmark' name='landmark' {...formik.getFieldProps('landmark')} />
                {formik.errors.landmark && formik.touched.landmark?<div style={{color:"red",fontSize:"10px"}}>{formik.errors.landmark}</div>:null}

                 </div>
                 <div className="col">
                    <label htmlFor='DeliveryInfo'>Additional Info</label>
                     <input className="bg-primary2 border-top-0 border-left-0 border-right-0 border-warning w-100" placeholder="Eg:(Do not ring the bell),(Backup phone number)" style={{display:"block"}} type='text' id='DeliveryInfo' name='DeliveryInfo' {...formik.getFieldProps('DeliveryInfo')} />
                     <small id="DeliveryInfo" className="form-text text-muted">(Optional)</small>
                 </div>
             </div>

            
             <div className="row my-3">
                 <div className='col-6 '>
                    <label htmlFor='tag'>Tag As</label>
                    <input className="bg-primary2 border-top-0 border-left-0 border-right-0 border-warning w-100" placeholder="Eg:Home,Office" style={{display:"block"}} type='text' id='tag' name='tag' {...formik.getFieldProps('tag')} />
                    <small id="tag" className="form-text text-muted">(Default:Home)</small>
                 </div>
                 <div className="col mt-4 mx-4">
                     {loading?
                     <div className="spinner-border" role="status">
                     <span className="sr-only">Loading...</span>
                 </div>:
                 null}
                 

                 </div>
                 
                
             </div>


             <div className="row my-3">

             <div className="col">
             <button className='btn btn-primary1 w-100 ' type="submit">Save Address</button>
             </div>
                

             </div>
             
        </form>
       </div>
               
            <div style={{fontSize:"1px"}}>Icons made by <a href="https://www.flaticon.com/authors/freepik" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
      </div>
    )
}
