import React,{useState} from 'react'
import {useDispatch} from 'react-redux'
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import {addCartProduct} from '../Redux/actions/CartActions'

export default function ProductDetailCard({product}) {
    const [weightSelected,setWeight]=useState(500)
    const [quantity,setQuantity]=useState(1)
    const dispatch=useDispatch()

    const handleSubmit=event=>{
        const CartInfo={"id":product.id,
                        "title":product.title,
                        "price":product.price,
                        "img":product.img,
                        "category":product.category,
                        "weight": weightSelected,
                        "quantity":quantity
                    }
        event.preventDefault();
        event.stopPropagation();
        dispatch(addCartProduct(CartInfo))
    }


    return(
            <Form onSubmit={handleSubmit}>
            
                <Form.Row>
                    <Form.Group as={Col} sm="5">
                    <h5>Weight:</h5>
                    </Form.Group>
                    <Form.Group as={Col} sm="7">
                    <Form.Control onChange={(event)=>setWeight(Number(event.target.value))}  className=" bg-primary2 border-primary1" as="select" id="selectWeight">
                        <option id="SHOIABGET" value="500" >500 grams</option>
                        <option value="1000" >1000 grams</option>
                        <option value="1500" >1500 grams</option>
                        <option value="2000">2000 grams</option>
                        <option value="2500">2500 grams</option>
                        <option value="3000">3000 grams</option>
                        <option value="3500">3500 grams</option>
                        <option value="4000">4000 grams</option>
                        <option value="4500">4500 grams</option>
                        <option value="5000">5000 grams</option>
                    </Form.Control>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                
                    <Form.Group as={Col} sm="5">
                    <h5>Quantity:</h5>
                    </Form.Group>
                    <Form.Group as={Col} sm="7">
                    
                    <div className="btn-group  bg-primary2" role="group" aria-label="Basic example">
                        <button style={{width:"25px",height:"40px"}} onClick={()=>setQuantity(prevState=>prevState==1?prevState:prevState-1)} type="button" className="btn btn-primary2 border-primary1 text-center"><p className="text-center">-</p></button>
                        <div style={{width:"25px",height:"40px"}} className="border-top border-bottom  border-primary1 pt-1 text-center">{quantity}</div>
                        <button style={{width:"25px",height:"40px"}} onClick={()=>setQuantity(prevState=>prevState+1)} type="button" className="btn border-primary1 text-center"><p className="text-center">+</p></button>
                    </div>
                    
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} sm="5">
                    </Form.Group>
                    <Form.Group as={Col} sm="7">
                    <Button className="btn btn-primary1" type="submit">
                        Add To Cart
                    </Button>
                    </Form.Group>
                </Form.Row>
                
            </Form>
            )
    }