import React from 'react'
import {connect} from 'react-redux'
import {useFormik} from 'formik'
import {signUpOTP} from '../Redux/actions/UserActions'
import Otp from './Otp'



export function SignUpFormWhatsapp({authenticated,SignUpUser,whatsappError,whatsappLoading,whatsappRes}) {
    
    const initialValues={name:'',
                        username:''
                    }

                    const onSubmit=values=>{
                        
                        SignUpUser(values.username,values.name)
                        
                    }
                    
                    const validate= values=>{
                       
                        let errors={}
                        if(!values.name){
                            errors.name= 'Required'
                        }
                        else if(!/^[a-z|A-Z|\s]+$/.test(values.name)){
                            errors.name='No Special characters and numbers are allowed'
                        }
                    
                        if(!values.username){
                           
                            errors.username= 'Required'
                        }
                        else if(!/^[0-9]*$/.test(values.username)){
                            errors.username='Only Digits Allowed'
                        }
                        else if(values.username.length!=10){
                            errors.username='Check PhoneNumber. 10 Digits are required.'
                        }
                        
                        return errors
                    }


    const formik=useFormik({
        initialValues,
        onSubmit,
        validate
    })
   
  
    return (
        <div>
            {authenticated?<h3 className="mt-4">Enjoy Your 10DerMeatz Experience! </h3>:
            whatsappRes?
            <div>
                <Otp location="SignUP" username={formik.values.username} name={formik.values.name}/>
                <b className="text-center text-muted">{whatsappRes}</b>
            </div>
            :
            <form onSubmit={formik.handleSubmit}>
                <label htmlFor='username'>Phone Number</label>
                <input className="bg-primary2 border border-primary1 rounded w-50"  style={{display:"block"}} type='tel' maxLength='10' id='username' name='username' {...formik.getFieldProps('username')} />
                {formik.errors.username && formik.touched.username?<div style={{color:"red",fontSize:"10px"}}>{formik.errors.username}</div>:<small className="form-text text-muted">(Enter Your Phone Number For Verification)</small>}

                <label htmlFor='name'> Name</label>
                <input className="bg-primary2 border border-primary1 rounded w-50"  style={{display:"block"}} type='text' id='name' name='name' {...formik.getFieldProps('name')} />
                {formik.errors.name && formik.touched.name && <div style={{color:"red",fontSize:"10px"}}>{formik.errors.name}</div>}

                <br/>
                
                
                <p className="text-danger">{whatsappError}</p>
                
                
                {whatsappLoading?
                 <button className="btn btn-primary1" type="button" disabled>
                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                    Loading...
                </button>
               :
                <button type="submit" className="btn btn-primary1">Sign Up</button>
               }
                
            </form>}
        </div>
       
    )
}

const mapStateToProps = state=>{
    return{whatsappLoading:state.user.whatsappLoading,
           whatsappError:state.user.whatsappError,
           whatsappRes:state.user.whatsappRes
           
          }
  }
  const mapDispatchToProps=dispatch => {
      return{
          SignUpUser:(username,firstname)=>dispatch(signUpOTP(username,firstname))
      }
  }
  
  export default connect(mapStateToProps,mapDispatchToProps)(SignUpFormWhatsapp)
  